import React from "react";
import useLocalStorage from "react-use-localstorage";
import { QRCode } from "react-qr-svg";
import "./app.css";

const defaultItemsText = `
 
  
   
  	 	 
 	 	
	 	 	  	
	 	 	   
`.trim();

export function Item({ value }) {
  return (
    <li className="item">
      <QRCode value={value || ""} className="qrcode" />
      <h2>{value}</h2>
    </li>
  );
}
export function Items({ items }) {
  return (
    <ul className="items">
      {items.map((e, i) => (
        <Item value={e} key={e + i} />
      ))}
    </ul>
  );
}
export default function App() {
  const isBrowser = () => typeof window !== "undefined";
  return isBrowser() ? _App() : "";
}
function _App() {
  const [itemsText, setItemsText] = useLocalStorage(
    "itemsText",
    defaultItemsText
  );
  const change = (e) => setItemsText?.(e.currentTarget.value);
  const items = itemsText?.trim()?.split(/\s+/) || [];
  return (
    <div className="root">
      <div className="control">
        <h1></h1>
        <h2></h2>
        <ol>
          <li></li>
          <li>
             Ctrl+P  Scale
          </li>
          <li></li>
          <li></li>
          <li></li>
        </ol>
      </div>
      <div className="control">
        <textarea onChange={change} value={itemsText} />
      </div>
      <div className="print">
        <Items items={items} />
      </div>
    </div>
  );
}
